import React, { Component } from 'react';
import { ListView, PullToRefresh, WhiteSpace } from 'antd-mobile';
import PrizeListItem from './PrizeListItem'

const data = Array.from(new Array(15)).map((_val, i) => ({
  img: 'http://img.zcool.cn/community/0193cc58ccefffa801219c7727a0cc.jpg@900w_1l_2o_100sh.jpg',
  title: `迪奥 Diro#${i}`,
}));

class PrizeList extends Component {
    constructor(props) {
        super(props);
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
          });
      
          this.state = {
            dataSource,
            refreshing: true,
            isLoading: false,
            height: document.documentElement.clientHeight,
            useBodyScroll: true,
          };
    }

    componentDidUpdate() {
        if (this.state.useBodyScroll) {
          document.body.style.overflow = 'auto';
        } else {
          document.body.style.overflow = 'hidden';
        }
    }

   genData(pIndex = 0) {
      const dataBlob = {};
      for (let i = 0; i < data.length; i++) {
        const ii = (pIndex * data.length) + i;
        dataBlob[`${ii}`] = `row - ${ii}`;
      }
      return dataBlob;
    }

    componentDidMount() {
      // you can scroll to the specified position
      // setTimeout(() => this.lv.scrollTo(0, 120), 800);
  
      // simulate initial Ajax
      setTimeout(() => {
        this.rData = this.genData();
        this.setState({
          dataSource: this.state.dataSource.cloneWithRows(this.rData),
          isLoading: false,
        });
      }, 600);
    }

    onRefresh = () => {
      this.setState({ refreshing: true, isLoading: true });
      // simulate initial Ajax
      setTimeout(() => {
        this.rData = this.genData();
        this.setState({
          dataSource: this.state.dataSource.cloneWithRows(this.rData),
          refreshing: false,
          isLoading: false,
        });
      }, 600);
    };

    render() {
        const separator = (sectionID, rowID) => (
            <div
              key={`${sectionID}-${rowID}`}
              style={{
                backgroundColor: 'rgba(250,245,247,1.0)',
                height: 8,
                borderTop: '1px solid #ECECED',
                borderBottom: '1px solid #ECECED',
              }}
            />
          );
          return (<div style={{background: 'rgba(250,245,247,1.0)', paddingLeft: '10px', paddingRight: '10px'}}>
            <WhiteSpace size="md" />
            <ListView
              key={this.state.useBodyScroll ? '0' : '1'}
              ref={el => this.lv = el}
              dataSource={this.state.dataSource}
              renderRow={(rowData, sectionID, rowID) => {
                const index = rowID;
                if(index < 0 || index >= data.length) {
                  index = data.length - 1;
                }
                const obj = data[index];
                return(
                <PrizeListItem itemData={obj} sectionID={sectionID} rowID={rowID} />
                )
              }}
              renderSeparator={separator}
              useBodyScroll={this.state.useBodyScroll}
              style={this.state.useBodyScroll ? {} : {
                height: this.state.height,
                border: '1px solid #ddd',
                margin: '5px 0',
              }}
              pullToRefresh={<PullToRefresh
                refreshing={this.state.refreshing}
                onRefresh={this.onRefresh}
              />}
              onEndReached={this.onEndReached}
              pageSize={5}
            />
          </div>);
    }
}

export default PrizeList